//asyn and await | error handling

async function makerequest() {
  try {
    console.log("Button Clicked");
    const res = await fetch("data.txt");
    if (!res.ok) {
      throw Error(res.statusText);
    }
    console.log(res);
    const data = await res.text();
    console.log(data);
  } catch (error) {
    console.log(error);
  }
}
